import React from 'react';
import ReactDOM from 'react-dom';
import App from './App';
import { arriving, departing } from './ft-data.json';
var targetContainerId = "widget__container";
export default {
  init: function init(config) {
    if (!document.getElementById(targetContainerId)) {
      var targetContainer = document.createElement('div');
      targetContainer.setAttribute('id', targetContainerId);
      document.body.appendChild(targetContainer);
    }

    ReactDOM.render(React.createElement(App, {
      apiKey: config.apiKey,
      authId: config.authId,
      env: config.env,
      arriveData: arriving,
      departData: departing,
      type: config.type
    }), document.getElementById(targetContainerId));
  },
  unmount: function unmount() {
    var targetContainer = document.getElementById(targetContainerId);

    if (targetContainer) {
      ReactDOM.unmountComponentAtNode(document.getElementById(targetContainerId));
    }
  }
};