// object of helper functions for formating display items
var flightValues = {
  getAirportValue: function getAirportValue(flighData, listType, type) {
    var airportName;

    if (listType === type) {
      airportName = "Norfolk VA, ORF";
    } else {
      airportName = "".concat(flighData.remote_city, " ").concat(flighData.remote_airport);
    }

    return airportName;
  },
  getGateLabel: function getGateLabel(flighData) {
    var termLabel = "";

    if (flighData.terminal && flighData.terminal.length > 0) {
      termLabel = "Term./";
    }

    return "".concat(termLabel, "Gate: ");
  },
  getStatus: function getStatus(flightData) {
    return flightData.status;
  },
  getGateValue: function getGateValue(flighData) {
    var termValue = "";
    var divider = "";

    if (flighData.terminal && flighData.terminal.length > 0) {
      termValue = flighData.terminal;
      divider = " / ";
    }

    var gateValue = flighData.gate;
    return termValue + divider + gateValue;
  },
  getTime: function getTime(flightData) {
    var date = new Date(flightData);
    var localeSpecificTime = date.toLocaleTimeString().replace(/:\d+ /, ' ');
    return localeSpecificTime;
  }
};
export default flightValues;